.main-header__toggle {
  position: relative;
  padding: 0;
  width: 24px;
  height: 16px;
  border: none;
  background-color: #ffffff;
  cursor: pointer;
}
@media (max-width: 767px) {
  .main-header--closed .main-header__toggle::after {
    content: "";
    height: 1.5px;
    width: 100%;
    background-color: #000000;
    position: absolute;
    top: 0;
    left: 0;
    box-shadow: 0 8px 0 0 #000000, 0 16px 0 0 #000000;
  }
  .main-header--opened .main-header__toggle::after,
  .main-header--opened .main-header__toggle::before {
    content: "";
    height: 2px;
    width: 25px;
    background-color: #000000;
    position: absolute;
    top: 8px;
    left: 0;
  }
  .main-header--opened .main-header__toggle::after {
    transform: rotate(45deg);
  }
  .main-header--opened .main-header__toggle::before {
    transform: rotate(-45deg);
  }
  .main-header--opened .main-header__main-nav {
    display: block;
    position: absolute;
    top: 65px;
  }
  .main-header--closed .main-header__main-nav {
    display: none;
  }
  .main-header--nojs .main-header__main-nav {
    display: block;
    position: relative;
    top: 0;
  }
  .main-header--nojs .main-header__toggle {
    display: none;
  }
}
